<template>
  <div>
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="billNumber">
          <el-input
            v-model="query.billNumber"
            filterable
            clearable
            placeholder="单号"
            style="width: 178px;"
            class="filter-item"
          />
        </el-form-item>
        <el-form-item prop="supplierId">
          <el-select
            v-model="query.supplierId"
            filterable
            clearable
            placeholder="去向(委托工厂)"
            style="width: 178px;"
            class="filter-item"
          >
            <el-option
              v-for="item in supplierPullList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item prop="startTime">
          <el-date-picker
            v-model="query.startTime"
            type="date"
            style="width: 178px;"
            class="filter-item"
            value-format="yyyy-MM-dd"
            placeholder="开始日期"
            @change="query.endTime = ''"
          />
        </el-form-item>
        <el-form-item prop="endTime">
          <el-date-picker
            v-model="query.endTime"
            type="date"
            style="width: 178px;"
            class="filter-item"
            value-format="yyyy-MM-dd"
            placeholder="结束日期"
            :picker-options="{disabledDate: (time) => time.getTime() < new Date(query.startTime).getTime()}"
          />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
        <el-button
          class="filter-item"
          type="primary"
          icon="el-icon-plus"
          @click="handleAdd"
        >新增</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="280"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <template v-if="[1, 5].includes(scope.row.status)">
            <el-button type="primary" @click="handle(scope.row, 'Submit')">提交</el-button>
            <el-button type="warning" @click="handleModify(scope.row)">修改</el-button>
            <el-button type="danger" @click="handle(scope.row, 'Delete')">删除</el-button>
          </template>
          <template v-if="scope.row.status === 2">
            <el-button type="warning" @click="handle(scope.row, 'Withdraw')">撤回</el-button>
          </template>
          <el-button type="primary" @click="handleDetail(scope.row)">详情</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
  </div>
</template>
<script>
import { getListApi, deleteApi, submitApi, withdrawApi } from '@/apis/modules/out-bound/outsourcing'
import { getPullListItemApi as getSupplierPullListApi } from '@/apis/modules/base/baseSupplier'
import paginationPage from '@/mixins/paginationPage'
import tableHeadSet from '@/mixins/tableHeadSet'
import Pagination from '@/components/Pagination'
import { returnOfGoodsMap } from '@/views/in-storage/inspection/status'
import { mapGetters } from 'vuex'

export default {
  components: {
    Pagination

  },
  mixins: [paginationPage, tableHeadSet],
  data() {
    return {
      query: {
        billNumber: '', // 外协单号
        supplierId: '', // 去向（供应商id）
        startTime: '', // 开始时间
        endTime: '' // 结束时间
      },
      title: this.$route.meta.title,
      loading: false,
      tableData: [],
      dialogVisible: false,
      returnOfGoodsMap: returnOfGoodsMap,
      columnSets: [{
        module: 'status',
        prop: 'status',
        formatter: (status) => {
          return returnOfGoodsMap[status - 1]
        },
        statusFormatter: (scope) => {
          const typeMap = {
            '1': 'info',
            '2': 'primary',
            '3': 'warning',
            '4': 'warning',
            '5': 'info'
          }
          return scope.status ? typeMap[scope.status] : ''
        } }],
      pullListConfig: [
        { api: getSupplierPullListApi(), dataKey: 'supplierPullList', name: '供应商' }
      ],
      supplierPullList: []
    }
  },
  computed: {
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  watch: {
    navbarWarehouseCheckedList: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.init()
    this.getPullList()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          page: {
            ...this.pageInfo
          },
          ...this.query,
          warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 页面下拉数据
    getPullList() {
      const apis = this.pullListConfig.map(c => c.api)
      Promise.all(apis)
        .then(result => {
          if (result.length) {
            result.forEach((c, i) => {
              const dataKey = this.pullListConfig[i].dataKey
              this[dataKey] = !c.code ? c.data : []
            })
          }
        })
        .catch(reason => {
          console.log(reason)
        })
    },
    // 新增
    handleAdd() {
      this.$router.push({
        name: 'BillOutsourceOutAdd'
      })
    },
    // 修改
    handleModify(item) {
      this.$router.push({
        name: 'BillOutsourceOutEdit',
        query: {
          billNumber: item.billNumber
        }
      })
    },
    // 操作按钮回调 提交、删除、撤回
    handle(item, name) {
      const obj = {
        Submit: { api: submitApi, tip: '该工单即将提交' },
        Delete: { api: deleteApi, tip: '该工单即将删除' },
        Withdraw: { api: withdrawApi, tip: '该工单即将撤回' }
      }
      this.$confirm(`${obj[name]['tip']}，是否继续？`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        obj[name]['api']({
          ...name === 'Delete' && { id: item.id, dataSources: item.dataSources },
          ...['Submit', 'Withdraw'].includes(name) && { billNumber: item.billNumber }
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '操作成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '操作已取消'
        })
      })
    },
    // 详情
    handleDetail(item) {
      this.$router.push({
        name: 'BillOutsourceOutDetail',
        query: {
          billNumber: item.billNumber
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

